import React, { Component } from 'react';
import ReactMapGL, { Marker, Popup } from 'react-map-gl';
import Pin from '../Pin/Pin';
import PinInfo from '../PinInfo/PinInfo';

import axios from 'axios';

const TOKEN = process.env.REACT_APP_MAPBOX_TOKEN;
const URL = process.env.NEXSTAFF_URL;

class Map extends Component {
  constructor(props) {
    super(props);

    this.state = {
      viewport: {
        width: 300,
        height: 200,
        latitude: 48.866667,
        longitude: 2.333333,
        zoom: 10
      },
      popupInfo: null,
      projects: []
    };
  }

  componentWillMount() {
    axios.get(`${URL}/km/services/assignments/complete`).then(result => {
      this.setState(() => ({
        projects: result.data
      }));
    });
  }

  componentDidMount() {
    window.addEventListener('resize', this._resize);
    this._resize();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this._resize);
  }

  _resize = () => {
    this.setState({
      viewport: {
        ...this.state.viewport,
        width: this.props.width || window.innerWidth - 15,
        height: this.props.height || window.innerHeight - 100
      }
    });
  };

  _renderCityMarker = (project, index) => {
    return (
      <Marker
        key={`marker-${index}`}
        longitude={parseFloat(project.longitude)}
        latitude={parseFloat(project.latitude)}
      >
        <Pin size={20} onClick={() => this.setState({ popupInfo: project })} />
      </Marker>
    );
  };

  _renderPopup() {
    const { popupInfo } = this.state;

    return (
      popupInfo && (
        <Popup
          tipSize={5}
          anchor="top"
          longitude={popupInfo.longitude}
          latitude={popupInfo.latitude}
          onClose={() => this.setState({ popupInfo: null })}
        >
          <PinInfo info={popupInfo} />
        </Popup>
      )
    );
  }

  render() {
    const { viewport, projects } = this.state;
    return (
      <div>
        <ReactMapGL
          {...viewport}
          onViewportChange={viewport => this.setState({ viewport })}
          mapboxApiAccessToken={TOKEN}
        >
          {projects.map(this._renderCityMarker)}
          {this._renderPopup()}
        </ReactMapGL>
      </div>
    );
  }
}

export default Map;
